import React from "react";

import { graphql, useStaticQuery } from "gatsby";
import { Helmet } from 'react-helmet';
import { GatsbyImage } from 'gatsby-plugin-image';

import Layout from "../components/layout_inner";
import Instagram from "../svg/instagram.svg";

const Contact = (props) => {
  const data = useStaticQuery(graphql`
    query {
      me: file(relativePath: { eq: "me2.jpg" }) {
        childImageSharp {
          gatsbyImageData(layout: CONSTRAINED, quality: 100, width: 1920, placeholder: TRACED_SVG)
        }
      }
    }
  `);

  return (
    <Layout {...props}>
      <Helmet title="Фотограф Минск | Контакты" defer={false} />
      <h1>
        Контакты
      </h1>
      <p>&nbsp;</p>
      <div className="main-page-container reverted">
        <GatsbyImage image={data.me.childImageSharp.gatsbyImageData} alt="Ольга Емельянцева" />
        <div className="main-page-content">
          <p>
            Снимаю в городе Минске и Минском районе. Выезжаю в другие города Беларуси.<br />
            Минимальный пакет для выездной съемки - 8 часов, к стоимости пакета добавляются расходы на топливо.
          </p>
          <p>&nbsp;</p>
          <p>
            МТС/Viber/Telegram
          </p>
          <p className="playfair">+375 33 665 19 16</p>
          <p>&nbsp;</p>
          <p>
            Я в инстаграм
            {' '}
            <a className="social-link" href="https://www.instagram.com/olga_emelyantsava" target="_blank" rel="noreferrer" aria-label="instagram"><Instagram width="25" heigth="25" /></a>
          </p>
          <p>&nbsp;</p>
        </div>
      </div>
    </Layout>
  );
};

export default Contact;
